@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(register) {
  padding: 25px 0 60px;
  background-color: $--background-color-page;

  @include e(inner) {
    width: $--content-width;
    margin: 0 auto;
    overflow: hidden;
    background-color: #181818;
    border-radius: 5px;
  }

  @include e(header) {
    height: 80px;
    padding: 0 60px;
    font-size: 22px;
    line-height: 80px;
    color: $--color-primary;
    background-color: #141414;
  }

  @include e(question) {
    float: right;
    font-size: 16px;
    color: #999;
  }

  @include e(link-service) {
    color: $--color-primary;

    &:hover {
      text-decoration: underline;
    }
  }

  @include e(content) {
    width: 890px;
    height: 554px;
    margin: 55px auto;
    background: #fff no-repeat left center;
    border-radius: 5px;

    @include background-image(url(~@/views/register/img/register_bg.gif));
  }

  @include e(box) {
    position: relative;
    float: left;
    width: 420px;
    height: 100%;
  }

  @include e(tab) {
    position: relative;
    width: 200px;
    height: 26px;
    margin: 40px auto 0;
    line-height: 26px;
    border-bottom: 1px solid #dbdbdb;

    &::after {
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 64px;
      height: 3px;
      content: '\20';
      border-bottom: 3px solid #cc2944;
      transition: all 0.3s ease;
    }

    &.right {
      &::after {
        transform: translateX(136px);
      }
    }
  }

  @include e(tab-item) {
    position: relative;
    height: 26px;
    font-size: 16px;
    line-height: 20px;
    color: #b0b0b0;

    &:hover,
    &.active {
      color: #696969;

      &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 3px;
        content: '\20';
        border-bottom: 3px solid #cc2944;
      }
    }

    &:hover:not(:hover) {
      &::after {
        display: none;
      }
    }
  }

  @include e(tips) {
    position: absolute;
    bottom: 40px;
    //left: 70px;
    width: 100%;
    font-size: 12px;
    color: #999;
    text-align: center;
  }
}

$--input-width: 270px;
$--input-height: 40px;
$--code-button-width: 96px;
$--input-prefix-width: 42px;
$--icon-url: url(~@/assets/img/common/common_icons_1.png);

@include b(form) {
  width: $--input-width;
  margin: 25px auto 0;

  @include e(error) {
    position: relative;
    height: 16px;
    margin-top: 5px;
    font-size: 12px;
    line-height: 16px;
    color: #eb4b4b;

    & + {
      @include e(label) {
        margin-top: 15px;
      }
    }
  }

  @include e(icon-status) {
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url(./img/icon_status.png) no-repeat;

    &.error {
      margin-right: 4px;
      vertical-align: top;
    }

    &.success {
      vertical-align: middle;
      background-position: -61px -144px;
    }
  }

  @include e(label) {
    display: block;
    margin-bottom: 6px;
    font-size: 16px;
    color: #696969;
  }

  @include e(input-wrapper) {
    position: relative;
    overflow: hidden;

    & + {
      @include e(label) {
        margin-top: 15px;
      }
    }
  }

  @include e(input) {
    width: $--input-width;
    height: $--input-height;
    padding-left: $--input-prefix-width + 10;
    font-weight: 700;
    line-height: $--input-height;
    color: #000;
    border: 1px solid #b7b7b7;
    transition: all 0.2s ease;

    &:disabled {
      background: #efefef;
    }

    &:focus {
      box-shadow: inset 0 0 3px rgba(183, 150, 117, 0.4);
    }

    &:hover:not(:disabled),
    &:focus:not(:disabled) {
      border: 1px solid #b79675;
    }

    &::placeholder {
      color: #b1b1b1;
    }

    @include m(code) {
      width: $--input-width - $--code-button-width -4;
    }
  }

  @include e(code) {
    width: $--code-button-width;
    height: $--input-height;
    margin-left: 4px;
    vertical-align: top;
    cursor: pointer;
  }

  @include e(prefix) {
    position: absolute;
    top: 50%;
    left: 0;
    z-index: 2;
    width: $--input-prefix-width;
    height: 18px;
    margin-top: -9px;
    background: no-repeat center/auto 18px;

    @include m(user) {
      background-image: url(./img/icon_user.png);
    }

    @include m(phone) {
      background-image: url(./img/icon_phone.png);
    }

    @include m(verify) {
      background-image: url(./img/icon_code.png);
    }

    @include m(line) {
      left: $--input-prefix-width;
      width: 1px;
      height: 20px;
      margin-top: -10px;
      border-right: 1px solid #e4e4e4;
    }
  }

  @include e(button) {
    display: block;
    margin-top: 22px;

    @include button();
    @include button-solid();
  }
}

@include b(dialog) {
  $--color-primary: #c6a45f;
  $--label-width: 68px;
  $--input-width: 280px;
  $--input-height: 40px;

  @include mask(10);

  @include e(inner) {
    position: relative;
    width: 620px;
    height: 520px;
    margin: 120px auto 0;
    overflow: hidden;
    background: no-repeat;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.4);

    @include background-image(url(~@/views/register/img/dialog_bg.jpg));

    @include m(success) {
      height: 400px;

      @include e(icon) {
        display: inline-block;
        width: 40px;
        height: 40px;
        margin: 0 16px 0 0;
      }

      @include e(button-wrapper) {
        margin-top: 40px;
        margin-left: 0;
        text-align: center;
      }
    }
  }

  @include e(close) {
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 38px;
    font-size: 44px;
    font-weight: 300;
    line-height: 0;
    color: #4d4d4d;
  }

  @include e(icon) {
    display: block;
    width: 60px;
    height: 60px;
    margin: 32px auto 0;
    vertical-align: top;
    background: no-repeat center/100%;

    @include background-image(url(~@/assets/img/common/icon_success_60x60.png));
  }

  @include e(logo) {
    display: block;
    height: 60px;
    margin: 50px auto 40px;
    vertical-align: top;
    background: no-repeat center/auto 100%;

    @include background-image(url(~@/assets/img/common/logo190x65.png));
  }

  @include e(title) {
    margin-top: 20px;
    font-size: 24px;
    line-height: 40px;
    color: $--color-primary;
    text-align: center;
  }

  @include e(subtitle) {
    margin-top: 20px;
    font-size: 14px;
    color: #ccc;
    text-align: center;
  }

  @include e(form) {
    width: $--label-width + $--input-width;
    margin: 25px auto 0;
  }

  @include e(form-item) {
    height: $--input-height;
    overflow: hidden;
    font-size: 16px;
    line-height: $--input-height;

    & + & {
      margin-top: 15px;
    }
  }

  @include e(label) {
    float: left;
    width: $--label-width;
  }

  @include e(input-wrapper) {
    position: relative;
    float: left;
    width: $--input-width;
    height: $--input-height;
    vertical-align: top;
  }

  @include e(input) {
    width: 100%;
    height: 100%;
    padding-left: 10px;
    border: 1px solid #8e8e8e;
    transition: all 0.2s ease;

    &:focus {
      box-shadow: inset 0 0 3px rgba(142, 142, 142, 0.4);
    }
  }

  @include e(account) {
    margin-left: 11px;
    color: $--color-primary;
  }

  @include e(position) {
    position: absolute;
    right: 0;
  }

  @include e(button-wrapper) {
    margin-top: 30px;
    margin-left: $--label-width;
  }

  @include e(button) {
    @include button($--input-width, 46px, 20px);

    @include m(solid) {
      @include button-solid();
    }

    @include m(hollow) {
      @include button-hollow();
    }

    &.small {
      width: ($--input-width - 20) / 2;

      & + & {
        margin-left: 20px;
      }
    }
  }

  @include e(newbie-gift) {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 60px;
    font-size: 16px;
    line-height: 60px;
    color: #fff;
    text-align: center;
  }

  @include e(gift-number) {
    margin: 0 10px;
    font-size: 28px;
    font-weight: 700;
    color: #cca352;
    vertical-align: top;
  }

  @include e(link) {
    color: $--color-primary;
    text-decoration: underline;
  }
}
